import { useState } from 'react';
import './App.css';
import AboutUs from './components/AboutUs/AboutUs';
import Footer from './components/Footer/Footer';
import FormModal from './components/FormModal/FormModal';
import Header from './components/Header/Header';
import Hero from './components/Hero/Hero';
import Menu from './components/Menu/Menu';
import Popular from './components/Popular/Popular';
import SpecialFU from './components/SpecialFU/SpecialFU'
function App() {
  const [menuModal, setMenuModal] = useState(false)
  const [orderModal, setOrderModal] = useState(false)
  const [orderStorage, setOrderStorage] = useState([])
  
  const onOrder = (prod) => {
    setOrderStorage(...orderStorage, prod)
    console.log(orderStorage);
  }
  return (
    <div className="App">
      <Header />
      <Hero onMenu={() => setMenuModal(true)} onOrder={() => setOrderModal(true)} />
      <Popular onAddProd={onOrder} />
      <AboutUs onOrder={() => setMenuModal(true)}/>
      <SpecialFU onAddProd={onOrder}/>
      <Footer />
      {menuModal && <Menu onExit={() => setMenuModal(false)} onOrder={() => {
        setMenuModal(false)
        setOrderModal(true)
      }} />}
      {orderModal && <FormModal onExit={() => setOrderModal(false)} onMenu={() => {
        setOrderModal(false)
        setMenuModal(true)
      }} order={orderStorage} />}
    </div>
  );
}

export default App;
