import Notiflix from 'notiflix'
import React, { useCallback, useEffect, useState } from 'react'
import { createPortal } from 'react-dom'
import style from './FormModal.module.sass'

const portal = document.querySelector('#portal')
  
const FormModal = ({ onExit, onMenu, order }) => {
  const [name, setName] = useState('')
  const [number, setPhone] = useState('')
  const [Address, setAddress] = useState('')
  const reset = () => {
    setName('')
    setPhone('')
    setAddress('')
  }
     const esc = useCallback(
        e => {
            if (e.code === `Escape`) {
                onExit()
            }
        },
        [onExit]
  )

  useEffect(() => {
    window.addEventListener('keydown', esc)

    return () => {
      window.removeEventListener('keydown', esc)
    }
  }, [esc])

  // const onBackClick = useCallback(
  //   e => {
  //     if (e.currentTarget === e.target) {
  //       onExit()
  //     }
  //   },
  //   [onExit]
  // )
   return createPortal(
     <div className={style.Overlay}
      //  onClick={onBackClick}
     >   
       <form className={style.Form} onSubmit={(e) => {
         e.preventDefault()
         if (order.length === 0) {
           return Notiflix.Notify.warning('Nothing in cart');
         }
         onExit()
         console.log(Address, number, name);
         reset()
       }} action="submit">
         <button type='click' onClick={onExit} className={style.ExitBtn}>x</button>
         <input className={style.Input}
           onChange={(e) => setName(e.currentTarget.value)}
           type="text"
           placeholder="Name" />
         <input className={style.Input}
           type="text"
           placeholder="Address"
           onChange={(e) => setAddress(e.currentTarget.value)}
         />
         <input className={style.Input}
           type="phone"
           placeholder="Phone"
            onChange={(e) => setPhone(e.currentTarget.value)}
         />
               <button className={style.MenuBtn} onClick={onMenu} type='click'>Menu</button>
                <ul className={style.List}>
                  <h4>Your order</h4>
                   {!order.length === 0 ? (order.map(({name, price}) => (<li><p>{name}</p> <p>{price }</p></li>))) : (<p>Nothing in cart</p>)
                   }
               </ul>
               <button className={style.OrderBtn} type="submit">Order</button>
           </form>
     </div>,
    portal
  )
}

export default FormModal